  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box; }

html {
  color: #444;
  direction: ltr;
  font-size: 1em;
  line-height: 1.4; }

html, html a {
  text-rendering: optimizeLegibility !important;
  /*-webkit-font-smoothing: antialiased !important;*/
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004); }

h1, h2, h3 {
  color: #1b1b1b;
  font-weight: normal; }

p {
  color: #555; }

a:hover, a:focus {
  outline: 0;
  text-decoration: none; }

/*
 * A better looking default horizontal rule
 */
hr {
  border: 0;
  border-top: 1px solid #ddd;
  height: 1px;
  margin: 1em 0;
  padding: 0; }

/*
 * Form elements
 */
button {
  font-size: 16px;
  padding: 10px 20px; }

textarea {
  height: 200px;
  padding-bottom: 15px;
  width: 100%; }

input, textarea {
  border: 1px solid #ccc;
  color: #888;
  font-size: 1em;
  padding: .6em 10px .6em 8px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  -webkit-transition: border 0.30s ease-in-out;
  -moz-transition: border 0.30s ease-in-out;
  -ms-transition: border 0.30s ease-in-out;
  -o-transition: border 0.30s ease-in-out;
  transition: border 0.2s ease-in-out;
  outline: none; }

input:focus, textarea:focus {
  border-color: #aaa;
  box-shadow: none;
  outline: 0;
  -webkit-box-shadow: none; }

label {
  color: #333;
  display: block;
  /*font-size: .9em;*/
  font-weight: normal;
  margin-bottom: .25em;
  text-transform: none; }

:focus {
  outline: #ff3366 dotted 0px; }

/*
PRINT
------------------------------------ */
@media print {
  /*
    Remove links
    -------------------------------- */
  a[href]:after {
    content: none !important; } }

body {
  background: #fff;
  text-align: center; }

.map {
  background: #ddd;
  height: 250px;
  margin: 30px auto;
  width: 300px; }
